import store from '@/store'

// 封装请求模块
const baseURL = 'http://localhost:8080'
// const baseURL = 'http://192.168.1.7:8080'
// const baseURL = 'http://192.168.0.192:8080'
// 添加拦截器
const httpInterceptor = {
    // 拦截前触发
    invoke(options) {
        // 1. 拼接基础路径
        if (!options.url.startsWith('http')) {
            options.url = baseURL + options.url
        }
        // 2. 请求头
        options.header = {
            ...options.header,
            'source-client': 'miniapp'
        }
        // 4. 获取token
        const token = store.getters['user/getToken']
        if (token) {
            options.header.Authorization = token
        }
    }
}

// 注册请求拦截器
uni.addInterceptor('request', httpInterceptor)
uni.addInterceptor('uploadFile', httpInterceptor)

// 封装请求函数
export const http = (options) => {
    // 1. 返回 Promise 对象
    return new Promise((resolve, reject) => {
        // 获取token
        const token = store.state.user.token
        
        // 如果不是登录请求且没有token，延迟100ms重试
        if (!options.url.includes('/login') && !token) {
            setTimeout(() => {
                http(options).then(resolve).catch(reject)
            }, 100)
            return
        }

        uni.request({
            ...options,
            // 2. 请求成功
            success(res) {
                // 2.1 处理后端返回的数据结构 (code,msg,data)
                if (res.statusCode === 200) {
                    // 业务状态码判断
                    if (res.data.code === 0) {
                        // 成功
                        resolve(res.data)
                    } else if (res.data.code === 1) {
                        // 业务失败
                        uni.showToast({
                            title: res.data.msg || '业务失败',
                            icon: 'none'
                        })
                        reject(res.data)
                    }
                } else if (res.statusCode === 401) {
                    // 401错误  -> 清除token，跳转到登录页
                    uni.showToast({
                        title: '请先登录',
                        icon: 'none'
                    })
                    store.dispatch('user/logout')
                    reject(res)
                } else {
                    // 其他错误
                    uni.showToast({
                        title: '服务异常',
                        icon: 'none'
                    })
                    reject(res)
                }
            },
            // 3. 请求失败
            fail(err) {
                console.log('请求详细信息:', {
                    url: options.url,
                    method: options.method,
                    data: options.data,
                    error: err
                })
                uni.showToast({
                    title: '网络错误，换个网络试试',
                    icon: 'none'
                })
                reject(err)
            }
        })
    })
} 